.hymn-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 3vh 6vw;

  & > div {
    flex: 1;
    text-align: center;

    &.header--left {
      text-align: left;
    }

    &.header--title {
      font-weight: bolder;
      font-size: 6vw;
      color: white;

      & > a {
        text-decoration: none;
        text-decoration-color: unset;
        color: inherit;
      }
    }

    &.header--right {
      text-align: right;
    }
  }

  img {
    width: 6vw;
    height: 5vw;
  }
}